@import url('https://fonts.googleapis.com/css2?family=Mooli&family=Roboto+Mono&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mooli", "Roboto Mono", monospace, sans-serif;
}

#layout{
  margin: 2rem 10rem;
}

/*––––––––––––––– NavBar ––––––––––––––*/
#nav-bar{
  display: flex;
  justify-content: space-between;
}

.link{
  text-decoration: none;
  margin: 0.2rem 0 0 1rem;
  transition: 0.2s ease;
  border-radius: .5rem;
  padding: 0.3rem .6rem;
  color: #3b3b3b;
}

.active{
  box-shadow: 2px 2px 5px #3b3b3b;
}

#menubar .link:hover{
  box-shadow: 2px 2px 5px #3b3b3b;
}

#menubar .link:active{
  color: #626262;
  box-shadow: 0px 0px 0px #3b3b3b;
}

@media (max-width: 800px){
  #layout{ margin: 2rem 5rem; }
}

@media (max-width: 500px){
  #layout{ margin: 2rem; }
}

/*––––––––––––––– Form Area ––––––––––––––*/

#form-container{
  display: flex;
  margin-top: 5rem;
  flex-direction: column;
  align-items: center;
}

#form-box{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40%;
  box-shadow: 3px 3px 5px #3b3b3b;
  border-radius: 1rem;
  padding: 1rem;
}

@media (max-width: 1100px){
  #form-box{
    width: 70%;
  }
}

@media (max-width: 700px){
  #form-box{
    width: 90%;
  }
}

/*––––––––––––––– input box ––––––––––––––*/

label{
  font-weight: bold;
  color: black;
  margin-top: 2rem;
}

#form-box div{
  display: flex;
  flex-direction: column;
}

input, select{
  border: none;
  border-bottom:1px solid #3b3b3b ;
  outline: none;
  padding: 0.5rem 1rem;
  width: 310px;
  font-size: large;
  color: #626262;
}

input[type="submit"], input[type="reset"]{
  background-color: aqua;
}

/*––––––––––––––– Buttons ––––––––––––––*/

.btn-container{
  margin-top: 2rem;
  width: 60%;
  flex-direction: row !important;
  justify-content: space-between !important;
}

.btn-container button, .resetPass{
  border: none;
  border-radius: 0.3rem;
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: 0.2s ease;
  background-color: rgb(114, 238, 217);
  font-size: medium;
}

button[type="reset"]{
  color: rgb(177, 210, 205);
  background-color: #626262;
}

.btn-container button:hover{
  box-shadow: 2px 2px 5px #3b3b3b;
}

.btn-container button:active{
  box-shadow: 2px 2px 2px #3b3b3b;
}

@media (max-width:1200px){
  input, select{
    width: 250px;
  }
}
